
html,body,div,span,applet,object,iframe,h1,h2,h3,h4,h5,h6,p,blockquote,pre,a,abbr,acronym,address,big,cite,code,del,dfn,em,img,ins,kbd,q,s,samp,small,strike,strong,sub,sup,tt,var,b,u,i,center,dl,dt,dd,ol,ul,li,fieldset,form,label,legend,table,caption,tbody,tfoot,thead,tr,th,td,article,aside,canvas,details,embed,figure,figcaption,footer,header,hgroup,menu,nav,output,ruby,section,summary,time,mark,audio,video,input{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;outline:none
}
article,aside,details,figcaption,figure,footer,header,hgroup,menu,nav,section{display:block
}
body{line-height:1
}
ol,ul{list-style:none
}
blockquote,q{quotes:none
}
blockquote:before,blockquote:after,q:before,q:after{content:'';content:none
}
table{border-collapse:collapse;border-spacing:0
}
body{background-color:#404351;color:#FFFFFA;font-family:'Cabin', sans-serif
}
button{background-color:#40C9A2;color:#fffffa;border:1px solid #40C9A2;border-radius:2px;padding:0.8em 2.5em;font-size:14px;text-transform:uppercase;letter-spacing:2px;font-weight:bold;line-height:1em;outline:none
}
button:hover{cursor:pointer;background-color:#5dd1b0
}
button.outline{background-color:transparent;border:1px solid #40C9A2;color:#40C9A2
}
button.outline:hover{cursor:pointer;background-color:#5d5f6b
}
.overworld{position:fixed;z-index:999;left:0;top:0;right:0;bottom:0;background-color:rgba(64,67,81,0.95);display:-ms-flexbox;display:flex
}
.overworld img{margin:auto
}
.value-item{text-align:center;border-right:1px solid #5d5f6b;padding:20px 0
}
.value-item:last-child{border-right:none
}
.value-item .label{text-transform:uppercase;font-size:13px;opacity:0.5;letter-spacing:1px
}
.value-item .value{font-size:30px;letter-spacing:1px;margin-top:20px
}
.value-item .value .dollar-sign{font-size:14px;position:relative;top:-1px;margin-right:2px
}
.value-item .value .cents{font-size:14px
}
.green{color:#44fea8
}
.red{color:#ff3d58
}
.bold{font-weight:bold
}
.up{color:#44fea8
}
.up::before{content:"+"
}
.up .percentChange::before{content:"+"
}
.down{color:#ff3d58
}
.assets-wrap table{width:100%;text-align:left;color:#ddd;font-size:14px
}
.assets-wrap th,.assets-wrap td{padding:17px 20px
}
.assets-wrap th{font-size:13px;color:#797b85;text-transform:uppercase;letter-spacing:1px
}
.assets-wrap th:hover{cursor:pointer;color:#a0a1a8
}
.assets-wrap tr:nth-child(2n) td{background-color:#515660
}
.assets-wrap tr:hover td{cursor:pointer;background-color:#555a63
}



